<template>
  <div>
    <div class="tabBox">
          <div class="tab">
            <router-link class="tabItem" active-class="active" to="/yeWuZongLan">业务总览</router-link>
            <div class="divideLine"></div>
            <div
              :class="`tabItem ${activeIndex === 1 ? 'active' : ''}`"
              @mouseenter="fxshowup"
              @mouseleave="fxshowend"
            >
              业务分析
              <div class="menu" v-if="fxshow">
                <router-link class="menuItem" to="/zhiNengHeYue">智能合约</router-link>
                <router-link class="menuItem" to="/dianFei">电费缴费</router-link>
                <router-link class="menuItem" to="/shuiFei">水费缴费</router-link>
                <router-link class="menuItem" to="/sheBao">社保医保缴费</router-link>
                <router-link class="menuItem" to="/pay">支付</router-link>
                <router-link class="menuItem" to="/huiNong">惠民惠农补贴</router-link>
              </div>
            </div>
            <div class="divideLine"></div>
            <router-link  :class="`tabItem ${activeIndex === 2 ? 'active' : ''}`" to="/trade">交易监控</router-link>
          </div>
          <div class="dateBox">
            <div class="label">数据日期</div>
            <div class="divideLine"></div>
            <div id="date" class="date" @click="setDate">
              {{datanow}}
            </div>
          </div>
          <div class="back" @click="goback">
            <div class="text">返回</div>
            <img src="../assets/images/DigitalPanel/right-arrow.png" alt="" />
          </div>
        </div>
        <div class="title">{{title}}</div>
  </div>
</template>

<script>
export default {
  props:{
    title:{required:true},
    activeIndex:{default:0},
  },
  data(){
    return{
      fxshow: false,
      datanow:'2021-07-21',
      //获取全局的laydate，带入到组件中
      laydate: window.laydate
    }
  },
  mounted() {
    this.setDate()
    console.log('columns',this.columns)
  },
  methods:{
    goback(){
      alert( "DigitalHeader" );
    },
    fxshowup(){
      this.fxshow = true;
      // console.log('fxshowup',this.fxshow)
    },
    fxshowend(){
      this.fxshow = false;
      // console.log('fxshowend',this.fxshow)
    },
    setDate() {
      const fillZero = (num) => (num.toString().length === 1 ? '0' + num : num)
        const date = new Date()
        this.laydate.render({
          elem: '#date',
          value: `${date.getFullYear()}-${fillZero(
          date.getMonth() + 1,
        )}-${date.getDate()}`,
        theme: '#102b3e',
        btns: ['confirm', 'now'],
          done: (value) => {
            this.date = value
          }
          
        })
      }
  }
}
</script>

