//引入VueRouter
import VueRouter from 'vue-router'
//引入Luyou 组件
import About from '../view/About'
import yeWuZongLan from '../view/yeWuZongLan'
import zhiNengHeYue from '../view/zhiNengHeYue'
import dianFei from '../view/dianFei'
import shuiFei from '../view/shuiFei'
import sheBao from '../view/sheBao'
import pay from '../view/pay'
import huiNong from '../view/huiNong'
import trade from '../view/trade'

//创建router实例对象，去管理一组一组的路由规则
const router = new VueRouter({
	routes:[
		{
			path:'/',
			redirect:'/yeWuZongLan'
		},
		{
			path:'/about',
			component:About
		},
		{
			path:'/yeWuZongLan',
			component:yeWuZongLan,
			meta: {
				title:"数字人民币业务总览"
			}
		},
		{
			path:'/zhiNengHeYue',
			component:zhiNengHeYue,
			meta: {
				title:"智能合约"
			}
		},
		{
			path:'/dianFei',
			component:dianFei,
			meta: {
				title:"电费缴费"
			}
		},
		{
			path:'/shuiFei',
			component:shuiFei,
			meta: {
				title:"水费缴费"
			}
		},
		{
			path:'/sheBao',
			component:sheBao,
			meta: {
				title:"社保医保缴费"
			}
		},
		{
			path:'/pay',
			component:pay,
			meta: {
				title:"支付业务分析"
			}
		},
		{
			path:'/huiNong',
			component:huiNong,
			meta: {
				title:"惠民惠农补贴发放"
			}
		},
		{
			path:'/trade',
			component:trade,
			meta: {
				title:"交易监控"
			}
		}
	]
})
router.beforeEach((to,from,next)=>{
    if(to.meta.title){
        document.title = to.meta.title;
    }
    next();
})
//暴露router
export default router