  methods: {
    //map
    test() {
      alert("yeWuZongLan");
    },
    map() {
      //构造地图数据
      const random = (min, max) =>
        Math.floor(Math.random() * (max - min + 1) + min);
      const OriginalMapData = HUNAN.features.map((item) => ({
        name: item.properties.name,
        value: item.properties.cp,
        data: random(100, 10000),
      }));
      echarts.registerMap("hunan", HUNAN);
      const map = echarts.init(this.$refs.map);
      // const map = echarts.init(document.getElementById('map'))
      const option = {
        animation: false, // 阻止拖拽和缩放时上下图层不同步
        geo: [
          {
            // 底图
            map: "hunan",
            id: "down",
            roam: false,
            zoom: 0.95,
            layoutCenter: ["50%", "52%"], // 地图位置
            layoutSize: "100%",
            itemStyle: {
              borderColor: "#265a78",
              borderWidth: 0.5,
              areaColor: "#235373",
              opacity: 0.5,
            },
            regions: [
              {
                name: this.city,
                itemStyle: {
                  areaColor: "#52a6ce",
                },
              },
            ],
          },
          {
            map: "hunan",
            id: "up",
            roam: false,
            zoom: 0.95,
            layoutCenter: ["50%", "50%"], // 地图位置
            layoutSize: "100%",
            itemStyle: {
              borderColor: "#7fd8e6",
              borderWidth: 1.5,
              areaColor: "#0a2235",
            },
            emphasis: {
              label: {
                show: false,
              },
              itemStyle: {
                areaColor: "#4ea0c9",
              },
            },
            regions: [
              {
                name: this.city,
                itemStyle: {
                  areaColor: "#52a6ce",
                },
              },
            ],
            zlevel: 1,
          },
        ],
        series: [
          {
            name: "地图",
            type: "map",
            geoIndex: 1,
            data: [],
          },
        ],
      };
      //自定义地图 label
      option.series.push({
        type: "scatter",
        coordinateSystem: "geo",
        label: {
          normal: {
            show: true,
            align: "center",
            verticalAlign: "middle",
            padding: [10, 10],
            formatter: (params) => {
              return params.data.name;
            },
            color: "#fff",
            fontSize: 16,
            backgroundColor: {
              type: "linear",
              x: 0,
              y: 0,
              x2: 0,
              y2: 1,
              colorStops: [
                {
                  offset: 0,
                  color: "#134e5a",
                },
                {
                  offset: 1,
                  color: "#0d2e42",
                },
              ],
            },
          },
          emphasis: {
            show: true,
          },
        },
        zlevel: 3,
        z: 999,
        data: OriginalMapData.filter((item) => item.name !== this.city),
      });
      for (let i = 0; i < OriginalMapData.length; i++) {
        const data = OriginalMapData[i];
        if (data.name === this.city) {
          //自定义当前选中城市的标签样式
          option.series.push({
            type: "scatter",
            coordinateSystem: "geo",
            symbolSize: 0,
            label: {
              normal: {
                show: true,
                align: "center",
                verticalAlign: "middle",
                padding: [10, 10],
                formatter: (params) => {
                  return params.data.name;
                },
                color: "#FFEA00",
                fontSize: 20,
                fontWeight: 900,
              },
              emphasis: {
                show: true,
              },
            },
            zlevel: 3,
            z: 999,
            data: [{ ...data }],
          });

          break;
        }
      }
      map.setOption(option);
      window.addEventListener("resize", () => {
        map.resize();
      });
      // alert("yeWuZongLan");
    },
  },
