<template>
  <div class="table-container">
      <table class="table">
          <thead class="table-head" v-if="showHead">
            <tr>
                <th
                  v-for="item in columns"
                  :class="item.class? 'table-cell ' + item.class: 'table-cell'"
                  :key="item.key"
                  :style= "{...item.style}" 
                  v-html="item.title"
                >
                </th>
            </tr>
          </thead>
        <tbody>
            <tr class="table-row" v-for="(item, row) in data" :key="`'row-'${row}`">
                <td
                  v-for="(col, index) in columns"
                  class="table-cell"
                  :key="col.key + '-' + index"
                  :style="{ ...col.style }"
                  v-html="diy(item,col,row)"
                >
                </td>
            </tr>
        </tbody>
      </table>
    </div>
</template>

<script>
export default {
  props:{
    columns:{required:true},
    data:{required:true},
    showHead:{
      default:true 
    }
  },
  data(){
    return{
      name: "test",
    }
  },
  mounted() {
    console.log('columns',this.columns)
  },
  methods:{
    diy(item,col,row){
      if(col.render){
        if(col.render.color){
          return `<span class="${col.render.class}" style="color: ${col.render.color[item[col.key]]}">
                ${item[col.key]}
                    </span>`
        }
        else{
          return `<span class="${col.render.class}" style="color: ${row > 2 ? '#A3B2B6' : '#7BFFC6'}">
                ${item[col.key]}
                    </span>`
        }
      }else{
        return item[col.key]
      }
    }
  }
}
</script>

